import React from "react";
import '../../hotel-page.scss'
import CallNowButton from "./CallNowButton";
import ThemeContext from "../../Context/ThemeContext";


class HotelPage extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <ThemeContext.Consumer>
                {(context) => {
                return (
                    <div className={`hotel-page-container ${context}`}>
                        <div className="hotel-content-container ">
                            <h1>{this.props.hotel.name}</h1>
                            <hr/>
                            <img className="hotelImg" src={this.props.hotel.img} />
                            <p>专: {this.props.hotel["street name"]}</p>
                            <p>专 驻住 专 住 , 拽住拽专专 驻住 转 砖注  住拽. 专 转 爪专拽 祝, 专 砖. 转专 住转注  住转砖 砖 - 转 专 专拽? 转 砖注住.</p>
                            <CallNowButton hotel={this.props.hotel} />
                            {/* <Context.Consumer>
                                
                                {(value) => <span>{value}</span>}
                                
                            </Context.Consumer> */}
                        </div>
                    </div>
                )}}
            </ThemeContext.Consumer>
        )
    }
}

export default HotelPage;